.text-content {

  h3 {
    @include heading-3();
    font-size: 24px;
  }

  ul {
    padding-left: 20px;
    list-style: none;

    li {
      margin-top: 22px;
      position: relative;

      &::before {
        content: '';
        display: block;
        position: absolute;
        left: -20px;
        top: 10px;
        border-radius: 50%;
        background: $color-green;
        width: 8px;
        height: 8px;
      }

      &:first-child {
        margin-top: 0;
      }
    }
  }

  p {
    margin: 24px 0 0;
  }
}