.object-description {

}

.object-description__bookmark-label {
  font-size: 18px;
  color: #3d3d3d;
  display: inline-block;
  margin-left: 28px;
  letter-spacing: 0.15px;
}

.object-description__table {
  width: 100%;
  display: block;
  margin-top: 36px;
  color: #3d3d3d;
}

.object-description__table-body {
  display: block;
}

.object-description__table-row {
  position: relative;
  display: flex;
  justify-content: space-between;
  margin-top: 16px;
}

.object-description__table-row::after {
  content: '';
  display: block;
  position: absolute;
  width: 100%;
  height: 1px;
  background: #C7D3DD;
  left: 0;
  bottom: 6px;
  z-index: 1;
}

.object-description__table-heading {
  text-align: left;
  font-weight: normal;
  display: block;
  background: $color-white;
  position: relative;
  z-index: 2;
  padding: 0 10px 0 0;
}

.object-description__table-value {
  text-align: right;
  display: block;
  background: $color-white;
  position: relative;
  z-index: 2;
  padding: 0 0 0 10px;
}

.object-description__actions {
  margin: 36px 0 0;
  display: flex;
}

.object-description__action {
  flex: 1;
}

.object-description__text-content {
  margin-top: 98px;
}

.object-description__price {
  margin: 36px 0 0;
  display: flex;
  align-items: baseline;
  flex-wrap: wrap;
}

.object-description__price-value {
  font-weight: 500;
  font-size: 42px;
  color: #343434;
}

.object-description__price-square {
  color: #3d3d3d;
  font-size: 18px;
  display: inline-block;
  margin-left: 30px;
}

.object-description__mortgage {
  display: flex;
}

.object-description__mortgage-type {
  flex: 1;
  margin-left: 40px;

  &:first-child {
    margin-left: 0;
  }
}

.object-description__mortgage-payment {
  display: block;
  color: #343434;
  margin-top: 18px;
  font-weight: 500;
  font-size: 28px;
}

.object-description__mortgage-type-label {
  color: #3d3d3d;
}

.object-description__mortgage-type-rate {
  font-size: 18px;
  color: #3d3d3d;
}

.object-description__mortgage-payment--link {
  color: $color-green;
}